// eslint-disable-next-line no-unused-vars
let timer, flag
/**
 * 
 *
 * @param {Function} func 
 * @param {Number} wait 
 * @param {Boolean} immediate 
 * @return null
 */
export default function throttle(func, wait = 500, immediate = true) {
  if (immediate) {
    if (!flag) {
      flag = true
      // wait
      typeof func === 'function' && func()
      timer = setTimeout(() => {
        flag = false
      }, wait)
    }
  } else {
    if (!flag) {
      flag = true
      // wait
      timer = setTimeout(() => {
        flag = false
        typeof func === 'function' && func()
      }, wait)
    }
  }
}
