// :
// yyyy:mm:dd|yyyy:mm|yyyymmdd|yyyymmdd hhMM,
export function timeFormat(dateTime = null, fmt = 'yyyy-mm-dd') {
  // null,
  if (!dateTime) dateTime = Number(new Date())
  // dateTime101313
  if (dateTime.toString().length == 10) dateTime *= 1000
  const date = new Date(dateTime)
  let ret
  const opt = {
    'y+': date.getFullYear().toString(), // 
    'm+': (date.getMonth() + 1).toString(), // 
    'd+': date.getDate().toString(), // 
    'h+': date.getHours().toString(), // 
    'M+': date.getMinutes().toString(), // 
    's+': date.getSeconds().toString() // 
    // 
  }
  for (const k in opt) {
    ret = new RegExp('(' + k + ')').exec(fmt)
    if (ret) {
      fmt = fmt.replace(ret[1], (ret[1].length == 1) ? (opt[k]) : (opt[k].padStart(ret[1].length, '0')))
    }
  }
  return fmt
}

/**
 * 
 * @param String timestamp 
 * @param String | Boolean format 
 * false
 */
export function timeFrom(dateTime = null, format = 'yyyy-mm-dd') {
  // null,
  if (!dateTime) dateTime = Number(new Date())
  // dateTime101313
  if (dateTime.toString().length == 10) dateTime *= 1000
  const timestamp = +new Date(Number(dateTime))

  const timer = (Number(new Date()) - timestamp) / 1000
  // 5,"",
  let tips = ''
  switch (true) {
    case timer < 300:
      tips = ''
      break
    case timer >= 300 && timer < 3600:
      tips = parseInt(timer / 60) + ''
      break
    case timer >= 3600 && timer < 86400:
      tips = parseInt(timer / 3600) + ''
      break
    case timer >= 86400 && timer < 2592000:
      tips = parseInt(timer / 86400) + ''
      break
    default:
      // formatfalsexx
      if (format === false) {
        if (timer >= 2592000 && timer < 365 * 86400) {
          tips = parseInt(timer / (86400 * 30)) + ''
        } else {
          tips = parseInt(timer / (86400 * 365)) + ''
        }
      } else {
        tips = timeFormat(timestamp, format)
      }
  }
  return tips
}


